import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Card from '@mui/material/Card';
import MuiChip from '@mui/material/Chip';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import Stack from '@mui/material/Stack';

import { styled } from '@mui/material/styles';

import DevicesRoundedIcon from '@mui/icons-material/DevicesRounded';
import EdgesensorHighRoundedIcon from '@mui/icons-material/EdgesensorHighRounded';
import ViewQuiltRoundedIcon from '@mui/icons-material/ViewQuiltRounded';

import SchoolIconUNCC from '../../assets/images/school_uncc.png'
import SchoolIconWakeTech from '../../assets/images/school_wt.jpg'

const items = [
  {
    degree: 'Bachelor of Computer Science',
    subheader: 'Concentration: Software, Systems, and Networks',
    school: 'University of North Carolina at Charlotte',
    graduated: 'May 2024',
    img: SchoolIconUNCC
  },
  {
    degree: 'Associate in Engineering',
    school: 'Wake Technical Community College',
    graduated: 'May 2021',
    img: SchoolIconWakeTech
  },
];

interface ChipProps {
  selected?: boolean;
}

const Chip = styled(MuiChip)<ChipProps>(({ theme }) => ({
  variants: [
    {
      props: ({ selected }) => selected,
      style: {
        background:
          'linear-gradient(to bottom right, hsl(210, 98%, 48%), hsl(210, 98%, 35%))',
        color: 'hsl(0, 0%, 100%)',
        borderColor: (theme.vars || theme).palette.primary.light,
        '& .MuiChip-label': {
          color: 'hsl(0, 0%, 100%)',
        },
        ...theme.applyStyles('dark', {
          borderColor: (theme.vars || theme).palette.primary.dark,
        }),
      },
    },
  ],
}));

interface MobileLayoutProps {
  selectedItemIndex: number;
  handleItemClick: (index: number) => void;
  selectedFeature: (typeof items)[0];
}

export function MobileLayout({
  selectedItemIndex,
  handleItemClick,
  selectedFeature,
}: MobileLayoutProps) {
  if (!items[selectedItemIndex]) {
    return null;
  }

  return (
    <Box
      sx={{
        display: { xs: 'flex', sm: 'none' },
        flexDirection: 'column',
        gap: 2,
      }}
    >
      <Box sx={{ display: 'flex', gap: 2, overflow: 'auto' }}>
        {items.map(({ title }, index) => (
          <Chip
            size="medium"
            key={index}
            label={title}
            onClick={() => handleItemClick(index)}
            selected={selectedItemIndex === index}
          />
        ))}
      </Box>
      <Card variant="outlined">
        <Box
          sx={(theme) => ({
            mb: 2,
            backgroundSize: 'cover',
            backgroundPosition: 'center',
            minHeight: 280,
            backgroundImage: 'var(--items-imageLight)',
            ...theme.applyStyles('dark', {
              backgroundImage: 'var(--items-imageDark)',
            }),
          })}
          style={
            items[selectedItemIndex]
              ? ({
                '--items-imageLight': items[selectedItemIndex].imageLight,
                '--items-imageDark': items[selectedItemIndex].imageDark,
              } as any)
              : {}
          }
        />
        <Box sx={{ px: 2, pb: 2 }}>
          <Typography
            gutterBottom
            sx={{ color: 'text.primary', fontWeight: 'medium' }}
          >
            {selectedFeature.title}
          </Typography>
          <Typography variant="body2" sx={{ color: 'text.secondary', mb: 1.5 }}>
            {selectedFeature.description}
          </Typography>
        </Box>
      </Card>
    </Box>
  );
}

export default function Education() {
  const [selectedItemIndex, setSelectedItemIndex] = React.useState(0);

  const handleItemClick = (index: number) => {
    setSelectedItemIndex(index);
  };

  const selectedFeature = items[selectedItemIndex];

  return (
    <Container sx={{
      pt: { xs: 2, sm: 4 },
      pb: { xs: 8, sm: 16 },
    }}>
      <Container
        sx={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          pt: { xs: 2, sm: 5 },
          pb: { xs: 2, sm: 3 },
        }}
      >
        <Typography
          id="header-education"
          variant="h3"
          sx={{ color: 'text.secondary' }}
        >
          Education
        </Typography>

      </Container>
      <Container sx={{ maxWidth: { xs: 100, sm: 600 } }} id='container-education'>
        <Box
          sx={{
            display: { xs: 'none', sm: 'flex' },
            flexDirection: 'column',
            gap: 2,
            height: '100%',
          }}
        >
          {items.map(({ degree, subheader, school, graduated, img }, index) => (
            <Box
              key={index}
              component={Button}
              onClick={() => handleItemClick(index)}
              variant='outlined'
              sx={[
                (theme) => ({
                  p: 2,
                  height: '100%',
                  width: '100%',
                  '&:hover': {
                    backgroundColor: (theme.vars || theme).palette.action.hover,
                  },
                }),
                selectedItemIndex === index && {
                  backgroundColor: 'action.selected',
                },
              ]}
            ><Stack
              direction="row"
              spacing={1}
              useFlexGap
              sx={{ justifyContent: 'left', color: 'text.secondary', alignItems: 'center', gap: 3 }}>
                <Box
                  component="img"
                  sx={{
                    height: 80,
                    width: 80,
                    maxHeight: { xs: 233, md: 167 },
                    maxWidth: { xs: 350, md: 250 },
                    borderRadius: '50%',
                    aspectRatio: 1,
                    objectFit: 'cover'
                  }}
                  alt={school}
                  src={img}
                />
                <Box
                  sx={[
                    {
                      minWidth: 330,
                      width: '100%',
                      display: 'flex',
                      flexDirection: 'column',
                      alignItems: 'left',
                      gap: 1,
                      textAlign: 'left',
                      textTransform: 'none',
                      color: 'text.secondary',
                    },
                    selectedItemIndex === index && {
                      color: 'text.primary',
                    },
                  ]}
                >
                  <Typography variant="h6">{degree}</Typography>
                  {subheader && <Typography variant="subtitle1">{subheader}</Typography>}
                  <Typography variant="body1">{school}</Typography>
                </Box>
                <Typography variant="body1" sx={{whiteSpace: 'nowrap', alignSelf: 'start' }}>{graduated}</Typography>
              </Stack>
            </Box>
          ))}
        </Box>
        <MobileLayout
          selectedItemIndex={selectedItemIndex}
          handleItemClick={handleItemClick}
          selectedFeature={selectedFeature}
        />
        <Box
          sx={{
            display: { xs: 'none', sm: 'flex' },
            width: { xs: '100%', md: '70%' },
            height: 'var(--items-image-height)',
          }}
        >
        </Box>
      </Container>
    </Container>
  );
}
