export default function Projects() {
  const theme = useTheme();
  const logos = theme.palette.mode === 'light' ? darkLogos : whiteLogos;

  const allUndefinedTagsSet = new Set<string>()
  projectsData.forEach(data => {
    data.tags.forEach(tag => {
      if (!predefinedTagColors[tag]) allUndefinedTagsSet.add(tag)
    })
  })
  const allUndefinedTags = Array.from(allUndefinedTagsSet).sort()
  const colorsByTag: Record<string, string> = {}
  const numTags = allUndefinedTags.length
  allUndefinedTags.forEach((tag, index) => {
    const t = index / numTags
    colorsByTag[tag] = `hsl(${t * 360}, 70%, 60%)`
  })
  Object.entries(predefinedTagColors).forEach(([tag, color]) => {
    colorsByTag[tag] = color
  })

  return (
    <Container
      id="testimonials"
      sx={{
        pt: { xs: 4, sm: 12 },
        pb: { xs: 8, sm: 16 },
        position: 'relative',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        gap: { xs: 3, sm: 6 },
        ...theme.applyStyles('dark', {

        }),
      }}
    >
      <Box id='DARK BOX' style={{
        width: '100%',
        height: 500,
        position: 'absolute',
        top: 0,
        zIndex: -1,
        backgroundImage:
          //'url(banner.png)',
          'radial-gradient(ellipse 80% 50% at 50% 50%, hsl(0, 0.00%, 0.00%), transparent)',
      }}>

      </Box>
      <Box
        sx={{
          width: { sm: '100%', md: '60%' },
          textAlign: { sm: 'left', md: 'center' },
        }}
      >
        <Typography
          component="h2"
          variant="h4"
          gutterBottom
          sx={{ color: 'text.primary' }}
          id="header-projects"
        >
          Projects
        </Typography>
        <Typography variant="body1" sx={{ color: 'text.secondary' }}>
          Check out some of my more presentable projects, both from hobby work and from university.
        </Typography>
      </Box>
      <Grid container spacing={2}>
        {projectsData.map((cardData, index) => {
          const defaultLink = cardData.serviceLink || cardData.sourceLink;
          return (
            <Grid size={{ xs: 12, sm: 6, md: 4 }} key={index} sx={{ display: 'flex' }}>
              <Card
                variant="outlined"
                sx={{
                  display: 'flex',
                  flexDirection: 'column',
                  justifyContent: 'start',
                  flexGrow: 1,
                  overflow: 'visible'
                }}
              >
                <CardMedia
                  sx={{
                    height: 240,
                    transition: 'transform 0.5s ease', // Smooth transition for scaling
                    '&:hover': {
                      transform: 'scale(1.5)', // Scale up on hover
                      transitionDelay: '0.2s',
                    },
                  }}
                  image={cardData.imageUrl}
                />
                <CardContent>
                  <Typography
                    variant="h4"
                    gutterBottom
                    sx={{ color: 'text.secondary' }}
                  >
                    {cardData.starred &&
                      <span style={{ color: 'white' }}>★ </span>}
                    {defaultLink
                      ? <a href={defaultLink} target="_blank">{cardData.projectName}</a>
                      : cardData.projectName}
                  </Typography>
                  {cardData.tags && cardData.tags.map((tag) => {
                    const color = colorsByTag[tag] || '#54afff';
                    return <Tag color={color}>
                      <Typography variant='body2' sx={{ color: color }}>{tag}</Typography>
                    </Tag>;
                  })}
                  <Typography
                    variant="body1"
                    gutterBottom
                    sx={{ color: 'text.secondary' }}
                  >
                    {cardData.description}
                  </Typography>
                </CardContent>
                {(cardData.serviceLink || cardData.sourceLink) && <CardActions sx={{ marginTop: 'auto' }}>
                  {cardData.serviceLink && <Button size="small" variant="outlined" href={cardData.serviceLink} target="_blank">Check it out!</Button>}
                  {cardData.sourceLink && <Button size="small" variant="outlined" href={cardData.sourceLink} target="_blank">Source</Button>}
                </CardActions>}
              </Card>
            </Grid>
          );
        })}
      </Grid>
    </Container>
  );
}
