import * as React from 'react';
import IconButton from '@mui/material/IconButton';
import Stack from '@mui/material/Stack';
import GithubIcon from '@mui/icons-material/GitHub';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import EmailIcon from '@mui/icons-material/Email';
import InsertDriveFileIcon from '@mui/icons-material/InsertDriveFile';
import { resumeLink } from '../../constants';

export default function LinkIconsRow() {
    return (
        <Stack
            direction="row"
            spacing={1}
            useFlexGap
            sx={{ justifyContent: 'left', color: 'text.secondary' }}
        >
            <IconButton
                color="inherit"
                size="small"
                href="https://github.com/mlasala45"
                target="_blank"
                aria-label="GitHub"
                sx={{ alignSelf: 'center' }}
            >
                <GithubIcon />
            </IconButton>
            <IconButton
                color="inherit"
                size="small"
                href="https://www.linkedin.com/in/micah-lasala/"
                target="_blank"
                aria-label="LinkedIn"
                sx={{ alignSelf: 'center' }}
            >
                <LinkedInIcon />
            </IconButton>
            <IconButton
                color="inherit"
                size="small"
                href="mailto:mlasala45@gmail.com"
                aria-label="Email Me"
                sx={{ alignSelf: 'center' }}
            >
                <EmailIcon />
            </IconButton>
            <IconButton
                color="inherit"
                size="small"
                href={resumeLink}
                target="_blank"
                aria-label="Resume"
                sx={{ alignSelf: 'center' }}
            >
                <InsertDriveFileIcon />
            </IconButton>
        </Stack>
    );
}